.cart-container{
    display: flex;
    flex-direction: column;
    padding: 5px;
}
.cart-product{
    display: flex;
    gap: 5px;
    align-items: center;
    margin-bottom: 10px;
}
.cart-product .img{
    width: 100px;
    height: 100px;
}
.cart-product .img img{
    width: 100%;
    height: 100%;
}